<template>
  <div class="container">
    <div class="row justify-content-center">
      <div class="register-form">
        <div class="logo-wrapper">
          <img class="logo" src="/static/images/logo.png">
          <div class="tagline">Open source task management tool</div>
        </div>
        <form @submit.prevent="submitForm">
          <div v-show="errorMessage" class="alert alert-danger failed">
            {{ errorMessage }}
          </div>
          <div class="form-group">
            <input type="text" class="form-control" id="username" v-model="form.username">
          </div>
          <div class="form-group">
            <input type="email" class="form-control" id="emailAddress" v-model="form.emailAddress">
          </div>
          <div class="form-group">
            <input type="password" class="form-control" id="password" v-model="form.password">
          </div>
          <div class="d-grid">
          <button type="submit" class="btn btn-primary">Create account</button>
          </div>
          <p class="accept-terms text-muted">By clicking “Create account”, you agree to our <a href="#">terms of service</a> and <a href="#">privacy policy</a>.</p>
          <p class="text-center text-muted">Already have an account? <a href="/login">Sign in</a></p>
        </form>
      </div>
    </div>
    <footer class="footer">
      <span class="copyright">&copy; 2022 TaskAgile.com</span>
      <ul class="footer-links list-inline float-end">
        <li class="list-inline-item"><a href="#">About</a></li>
        <li class="list-inline-item"><a href="#">Terms of Service</a></li>
        <li class="list-inline-item"><a href="#">Privacy Policy</a></li>
        <li class="list-inline-item"><a href="https://github.com/taskagile/vuejs.spring-boot.mysql" target="_blank">GitHub</a></li>
      </ul>
    </footer>
  </div>
</template>

<script>
import registrationService from '@/services/registration'
import useVuelidate from '@vuelidate/core'
import { required, email, minLength, maxLength, alphaNum } from '@vuelidate/validators'

export default {
  name: 'RegisterPage',
  setup () {
    return { v$: useVuelidate() }
  },
  data () {
    return {
      form: {
        username: '',
        emailAddress: '',
        password: ''
      },
      errorMessage: ''
    }
  },
  validations () {
    return {
      form: {
        username: {
          required,
          minLength: minLength(2),
          maxLength: maxLength(50),
          alphaNum
        },
        emailAddress: {
          required,
          email,
          maxLength: maxLength(100)
        },
        password: {
          required,
          minLength: minLength(6),
          maxLength: maxLength(30)
        }
      }
    }
  },
  methods: {
    submitForm () {
      this.v$.$touch()
      if (this.v$.$invalid) {
        return
      }

      registrationService.register(this.form).then(() => {
        this.$router.push({ name: 'LoginPage' })
      }).catch((error) => {
        this.errorMessage = 'Failed to rergister user. Reason: ' + (error.message ? error.message : 'Unknown') + '.'
      })
    }
  }
}

</script>

